<template>
  <div class="main">
    <task-header>
      <span style="font-size:16px;"></span>
    </task-header>
    <div style="text-align:left">     
      <my-date-input :width='100' label="" v-model="startDate"></my-date-input>
     ~
      <my-date-input :width='100'  v-model="stopDate" label=""></my-date-input>
      <mu-button @click="doTongji" color="red" small></mu-button>
    </div>    
    <div ref="summaryContentDiv" style="overflow:auto;">
      <fieldset v-for="item in summary">
        <legend>{{item.type}}</legend>
        <mu-data-table :columns="columns" :data="item.data">
          <template slot-scope="scope">
            <td>{{scope.row.key}}</td>
            <td class="is-right">{{scope.row.distance}}</td>
            <td class="is-right">{{scope.row.times}}</td>
            <td class="is-right">{{scope.row.places}}</td>
          </template>
        </mu-data-table>
      </fieldset>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import TaskHeader from "./TaskHeader";
import MyDateInput from "@/components/MyDateInput";
export default {
  name: "Summary",
  components: {
    TaskHeader,
    MyDateInput
  },
  computed: {
    ...mapState(["userinfo", "r"])
  },
  data() {
    return {
      date:undefined,
      startDate: undefined,
      stopDate: undefined,
      columns: [
        { title: "", width: 105, name: "subject" },
        {
          title: "(km)",
          name: "distance",
          width: 100,
          align: "center",
          sortable: true
        },
        {
          title: "",
          name: "times",
          width: 50,
          align: "center",
          sortable: true
        },
        {
          title: "",
          name: "places",
          width: 100,
          align: "center",
          sortable: true
        }
      ],
      summary: []
    };
  },
  methods: {
    async doTongji() {
      // console.log(this.startDate);
      // console.log(this.stopDate);
            
      const resp = await this.r.post("getsummary", {
        d_min: this.startDate.toLocaleDateString(),
        d_max: this.stopDate.toLocaleDateString()
      });
      // console.log(resp.data);
      this.summary = resp.data;
    }
  }
};
</script>
<style lang="stylus" scoped>
.main {
  height: 100%;
  display: flex;
  flex-direction: column;

  .summaryContentDiv {
    flex: 1 1 auto;
    overflow: auto;
  }

  fieldset{
    padding 0
  }
}
</style>
